.container.testimonial_container{
    width: 40%;
    margin-top: 5rem;
}
.client_avatar{
width: 4rem;
aspect-ratio: 1/1;
overflow: hidden;
border-radius: 50%;
margin: 0 auto 1rem;
border: 0.4rem solid var(--color-primary-varient);

}
.testimonal{
    background: var(--color-bg-varient);
    text-align: center;
padding: 2rem;
border-radius: 2rem;
user-select: none;
margin: 1rem auto;
width: 70%;
}
.head_testimonial{
    text-align: center;
}
.head_testimonial h2{
    color: var(--color-primary);
}
.head_testimonial h5{
    color: var(--color-light);

}
.client_review{
    color: var(--color-light);
    font-weight: 300;
    display: block;
    width: 80%;
    margin: 0.8rem auto 0;
}

@media screen and (max-width:1024px) {
    .container.testimonial_container{
        width: 40%;
    }
}
@media screen and (max-width:600px) {
    .container.testimonial_container{
        width:var(--container-width-sm);

    }
    .client_review{
        width:var(--container-width-sm);
    }
}