footer{
    text-align: center;
    background-color: var(--color-primary);
    margin-top: 7rem;
    padding: 3rem 0;
    font-size: 0.9rem;
}
footer a{
    color: var(--color-bg);
}
.footer_logo{
    font-size:2rem;
    font-weight: 500;
    margin-bottom: 2rem;
    display: inline-block;
}
.premalink{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 2rem;
    margin: 0 auto 3rem;
}
.footer_social{
    display: flex;
    justify-content: center;
    gap: 1rem;
    margin-bottom: 4rem;
}
.footer_social a{
    background-color: var(--color-bg);
    color: var(--color-white);
    padding: 0.8rem;
    border-radius: 0.7rem;
    display: flex;
    border: 1px solid transparent;
}
.footer_social a:hover{
    background-color: transparent;
    color: var(--color-bg);
    border-color: var(--color-bg);
}
.footer_copyright{
    margin-bottom: 4rem;
    color: var(--color-bg);
}

@media screen and (max-width:600px) {
    .premalink{
        flex-direction: column;
        gap: 1.5rem;
    
    }
    .footer_social{
        margin-bottom: 2rem;
    }
}