nav{
    background-color: rgba(0, 0, 0, 0.3);
    width: max-content;
padding: 0.7rem 1.7rem;
z-index: 2;
display: block;
position:fixed;
bottom:2rem;
left: 50%;
transform: translateX(-50%);
display: flex;
gap: 0.8rem;
border-radius: 3rem;
backdrop-filter:blur(15px);

}

nav a{
    background: transparent;
    padding: 0.9rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-light);
    font-size: 1.1rem;
}

nav a:hover{
    background-color: rgba(0, 0, 0, 0.2);
}

nav a.active{
    background-color: var(--color-primary);
color: var(--color-bg);
}