header{
    height:100vh ;
    padding-top: 5rem;
    overflow: hidden;
}
.header_container{
    text-align: center;
    height: 100%;
    position: relative;
    color: var(--color-white);
}

/* ++++++++=========CTA ========="++++++ */

.cta{
    margin-top: 2.5rem;
    display: flex;
    justify-content: center;
    gap: 1.2rem;
}

/* ==========header social */

.header__social{
    display: flex;
    flex-direction: column;
position: absolute;
align-items: center;
gap: 0.8rem;
left: 0;
bottom: 3rem;
}

.header__social::after{
    content: "";
    width: 1px;
    height: 2rem;
    background: var(--color-primary);

}

/* ================ME============== */
.me{
    background:linear-gradient(var(--color-primary),transparent);
    /* padding: 2rem 1.5rem 1.5rem 1.5rem; */
    overflow: hidden;
    width: 22rem;
    height: 33rem;
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    position: absolute;
    left:calc(50% - 11rem);

}
.me img{
    background-size: cover;
}


/* ============scrolldown========== */

.scroll__down{
    position: absolute;
    right: -2.3rem;
    transform: rotate(90deg);
    /* flex-direction: column; */
    justify-content: center;
    bottom: 5rem;
    font-weight: 300;
    font-size: 0.9rem;
}

/*==============Media Queries (medium Devices) */


/*==============Media Queries (small Devices) */


@media screen and (max-width:600px) {
    
    header{
        height: 100vh;
    }
    .header__social,
    .scroll__down{
display:none;

    }

}